@media (min-width: 1200px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl {
    max-width: 1200px;
  }
}
.page #banner {
  position: relative;
}
.page #banner .texts {
  position: absolute;
  bottom: 20%;
  font-weight: bold;
}
.page #banner .texts h1 {
  font-size: 30px;
  line-height: 37px;
  color: #fff;
  margin-bottom: 3px;
  font-weight: bold;
}
.page #banner .texts h1 span {
  color: #cae098;
}
.page #banner .texts p {
  font-size: 20px;
  line-height: 24px;
  color: #fff;
}
.page#page-sobre-nos .container-sobre {
  position: relative;
  background: #006635;
  z-index: 0;
}
.page#page-sobre-nos .container-sobre::before {
  content: "";
  position: absolute;
  top: 2%;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("../../assets/images/before_sobre_sec1.png") no-repeat left bottom;
  background-size: contain;
  opacity: 0.2;
  z-index: -1;
}
.page#page-sobre-nos #video {
  background: #d9d9d9;
  padding-bottom: 80px;
}